@-webkit-keyframes spin /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes spin {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@mixin animate-all{
	-webkit-transition: all 0.2s ease-in;
	-moz-transition: all 0.2s ease-in;
  -ms-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
	transition: all 0.2s ease-in;
}
@mixin animate-bg{
	-webkit-transition: background 0.2s ease-in;
	-moz-transition: background 0.2s ease-in;
    -ms-transition: background 0.2s ease-in;
    -o-transition: background 0.2s ease-in;
	transition: background 0.2s ease-in;
}
@mixin spin {
	-webkit-animation: spin 2s infinite linear;
	animation: spin 2s infinite linear;
}
@mixin animate-height{
	-webkit-transition: height 0.2s ease-in;
	-moz-transition: height 0.2s ease-in;
    -ms-transition: height 0.2s ease-in;
    -o-transition: height 0.2s ease-in;
	transition: height 0.2s ease-in;
}
@mixin border($style,$width,$color){ border:$style $width $color}
@mixin border-radius($radius){
	-webkit-border-radius:$radius;
	-ms-border-radius:$radius;
	-moz-border-radius:$radius;
	border-radius:$radius;
}

@mixin animate-width{
	-webkit-transition: width 0.5s ease-in;
	-moz-transition: width 0.5s ease-in;
    -ms-transition: width 0.5s ease-in;
    -o-transition: width 0.5s ease-in;
	transition: width 0.5s ease-in;
}
@mixin drop-shadow($color){
	-webkit-box-shadow: 0 1.5px 1.1px -1px $color;
	-moz-box-shadow: 0 1.5px 1.1px -1px $color;
	box-shadow: 0 1.5px 1.1px -1px $color;
}
@mixin no-shadow(){
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
}
@mixin text-shadow($color){
	-webkit-text-shadow:0.5px 0.5px 1.5px $color;
	-moz-text-shadow:0.5px 0.5px 1.5px $color;
	text-shadow:0.5px 0.5px 1.5px $color;
}
@mixin clear-input {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	-webkit-background-clip: padding;
	-moz-background-clip: padding;
	background-clip:padding-box;
	-webkit-border-radius:0;
	-moz-border-radius:0;
	-ms-border-radius:0;
	-o-border-radius:0;
	border-radius:0;
	-webkit-appearance:none;
	outline:0;
	margin:0;
	padding:0;
	vertical-align: middle;
}
@mixin -br($width, $rp : ''){
  -webkit-border#{$rp}-radius: $width;
  -ms-border#{$rp}-radius: $width;
  -moz-border#{$rp}-radius: $width;
  border#{$rp}-radius: $width;
}
@function in-list($value, $list) {
	@return (null != list.index($list, $value));
}
@-webkit-keyframes
ripple { to {
   -webkit-transform: scale(1.5);
   transform: scale(1.5);
   opacity: 0;
  }
}
@keyframes
ripple { to {
   -webkit-transform: scale(1.5);
   transform: scale(1.5);
   opacity: 0;
  }
}